
.lp-content-item {
    position: relative;
}

.lp-empty-content-item:after,
.lp-over-content-item:after {
    content: 'Replace ' attr(data-type);
    min-height: 98px;
    text-shadow: 1px 1px 0 white;
    display: block;
    padding: 30px;
    text-align: center;
    border: 1px solid #ddd;
    text-transform: uppercase;
    font-weight: bold;
    color: #999;
    background: #f0f0f0;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
.lp-over-content-item:after {
    position: absolute;
    background-color: #ddd;
    color: #fff;
    opacity: .9;
    text-shadow: -1px -1px 0 rgba(0,0,0,.5);
}

.lp-empty-content-item:after {
    content: 'Drop ' attr(data-type) ' here';
}
